@import '../Common/SCSS/variables';

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-image: url("https://www.desktopbackground.org/p/2010/05/31/25998_black-abstract-wallpapers-wide_4288x2416_h.jpg");
    grid-area: h;
    border: $textColor 1px solid;
    border-radius: 7px;
    margin-top: 5px;
}




.userName {
    margin-top: 20px;
    color: $textColor;
    font-size: 40px;
}

.btnLog {
    margin: 10px 15px 10px 400px;
    background: $goldColor;
    color: $blackColor;
    font-weight: bolder;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90px;
    height: 30px;

}